"use client";

import { useState, useRef, useEffect, useCallback } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Badge } from "@/components/ui/badge";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Progress } from "@/components/ui/progress";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { ScoreCard } from "./score-card";
import { toast } from "sonner";

// Interview phases configuration
const INTERVIEW_PHASES = [
  { id: "requirements", name: "Requirements", duration: 8, startTime: 45, endTime: 37, color: "bg-blue-500" },
  { id: "high-level", name: "High-Level Design", duration: 12, startTime: 37, endTime: 25, color: "bg-green-500" },
  { id: "deep-dive", name: "Deep Dive", duration: 12, startTime: 25, endTime: 13, color: "bg-purple-500" },
  { id: "scalability", name: "Scalability", duration: 10, startTime: 13, endTime: 3, color: "bg-orange-500" },
  { id: "wrapup", name: "Wrap-up", duration: 3, startTime: 3, endTime: 0, color: "bg-gray-500" },
] as const;

const TOTAL_INTERVIEW_TIME = 45 * 60; // 45 minutes in seconds

interface Message {
  id: string;
  role: "user" | "assistant";
  content: string;
  timestamp: string;
}

interface PhaseAnalysis {
  phase: string;
  score: number;
  summary: string;
  strengths: string[];
  improvements: string[];
}

interface Score {
  requirementsClarification: number;
  highLevelDesign: number;
  detailedDesign: number;
  scalability: number;
  tradeoffs: number;
  communication: number;
  overallScore: number;
  passStatus: boolean;
  feedback: {
    requirementsClarification: string;
    highLevelDesign: string;
    detailedDesign: string;
    scalability: string;
    tradeoffs: string;
    communication: string;
    overallFeedback: string;
    strengths: string[];
    improvements: string[];
  };
}

interface InterviewChatProps {
  interview: {
    id: string;
    topic: string;
    difficulty: string;
    status: string;
    startedAt: string | null;
  };
  initialMessages: Message[];
  score: Score | null;
}

export function InterviewChat({
  interview,
  initialMessages,
  score: initialScore,
}: InterviewChatProps) {
  const router = useRouter();
  const [messages, setMessages] = useState<Message[]>(initialMessages);
  const [input, setInput] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [isEnding, setIsEnding] = useState(false);
  const [showEndDialog, setShowEndDialog] = useState(false);
  const [score, setScore] = useState<Score | null>(initialScore);
  const scrollRef = useRef<HTMLDivElement>(null);

  // Timer and phase states
  const [remainingTime, setRemainingTime] = useState(TOTAL_INTERVIEW_TIME);
  const [currentPhaseIndex, setCurrentPhaseIndex] = useState(0);
  const [phaseAnalyses, setPhaseAnalyses] = useState<PhaseAnalysis[]>([]);
  const [isAnalyzingPhase, setIsAnalyzingPhase] = useState(false);
  const [phaseStartMessageIndex, setPhaseStartMessageIndex] = useState(0);
  const [showTimeWarning, setShowTimeWarning] = useState<string | null>(null);

  // Voice input states
  const [isListening, setIsListening] = useState(false);
  const [transcript, setTranscript] = useState("");
  const [inputMode, setInputMode] = useState<"voice" | "keyboard">("voice");
  const [speechSupported, setSpeechSupported] = useState(false);
  const recognitionRef = useRef<SpeechRecognition | null>(null);

  const isCompleted = interview.status === "completed" || score !== null;
  const currentPhase = INTERVIEW_PHASES[currentPhaseIndex];

  // Check for speech recognition support
  useEffect(() => {
    if (typeof window !== "undefined") {
      const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
      setSpeechSupported(!!SpeechRecognition);
    }
  }, []);

  // Initialize speech recognition
  useEffect(() => {
    if (typeof window === "undefined" || !speechSupported) return;

    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    if (!SpeechRecognition) return;

    const recognition = new SpeechRecognition();
    recognition.continuous = true;
    recognition.interimResults = true;
    recognition.lang = "en-US";

    recognition.onresult = (event) => {
      let interimTranscript = "";
      let finalTranscript = "";

      for (let i = event.resultIndex; i < event.results.length; i++) {
        const transcript = event.results[i][0].transcript;
        if (event.results[i].isFinal) {
          finalTranscript += transcript;
        } else {
          interimTranscript += transcript;
        }
      }

      if (finalTranscript) {
        setInput((prev) => prev + finalTranscript);
        setTranscript("");
      } else {
        setTranscript(interimTranscript);
      }
    };

    recognition.onerror = (event) => {
      console.error("Speech recognition error:", event.error);
      if (event.error === "not-allowed") {
        toast.error("Microphone access denied. Please allow microphone access.");
        setInputMode("keyboard");
      }
      setIsListening(false);
    };

    recognition.onend = () => {
      if (isListening) {
        try {
          recognition.start();
        } catch {
          // Ignore errors when restarting
        }
      }
    };

    recognitionRef.current = recognition;

    return () => {
      recognition.stop();
    };
  }, [speechSupported, isListening]);

  // Countdown timer
  useEffect(() => {
    if (isCompleted) return;

    const interval = setInterval(() => {
      setRemainingTime((prev) => {
        if (prev <= 0) {
          clearInterval(interval);
          return 0;
        }

        const newTime = prev - 1;
        const minutesLeft = Math.floor(newTime / 60);

        // Time warnings
        if (minutesLeft === 30 && newTime % 60 === 0) {
          setShowTimeWarning("30 minutes remaining");
          setTimeout(() => setShowTimeWarning(null), 3000);
        } else if (minutesLeft === 15 && newTime % 60 === 0) {
          setShowTimeWarning("15 minutes remaining - Move to scalability discussion");
          setTimeout(() => setShowTimeWarning(null), 4000);
        } else if (minutesLeft === 5 && newTime % 60 === 0) {
          setShowTimeWarning("5 minutes remaining - Start wrapping up");
          setTimeout(() => setShowTimeWarning(null), 4000);
        } else if (minutesLeft === 1 && newTime % 60 === 0) {
          setShowTimeWarning("1 minute remaining!");
          setTimeout(() => setShowTimeWarning(null), 3000);
        }

        // Auto phase transition based on time
        const newPhaseIndex = INTERVIEW_PHASES.findIndex(
          (phase) => minutesLeft < phase.startTime && minutesLeft >= phase.endTime
        );
        if (newPhaseIndex !== -1 && newPhaseIndex !== currentPhaseIndex) {
          handlePhaseTransition(newPhaseIndex);
        }

        return newTime;
      });
    }, 1000);

    return () => clearInterval(interval);
  }, [isCompleted, currentPhaseIndex]);

  // Auto-scroll to bottom
  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [messages]);

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, "0")}:${secs.toString().padStart(2, "0")}`;
  };

  const getTimeColor = () => {
    const minutes = remainingTime / 60;
    if (minutes <= 5) return "text-red-500";
    if (minutes <= 15) return "text-yellow-500";
    return "text-foreground";
  };

  const handlePhaseTransition = async (newPhaseIndex: number) => {
    // Analyze the completed phase
    if (messages.length > phaseStartMessageIndex + 1) {
      setIsAnalyzingPhase(true);
      const phaseMessages = messages.slice(phaseStartMessageIndex);

      try {
        const response = await fetch(`/api/interview/${interview.id}/analyze-phase`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            phaseId: currentPhase.id,
            phaseMessages: phaseMessages.map((m) => ({
              role: m.role,
              content: m.content,
            })),
          }),
        });

        if (response.ok) {
          const data = await response.json();
          setPhaseAnalyses((prev) => [...prev, data.analysis]);
          toast.success(`${currentPhase.name} phase analyzed`);
        }
      } catch (error) {
        console.error("Phase analysis error:", error);
      } finally {
        setIsAnalyzingPhase(false);
      }
    }

    setCurrentPhaseIndex(newPhaseIndex);
    setPhaseStartMessageIndex(messages.length);
    toast.info(`Moving to ${INTERVIEW_PHASES[newPhaseIndex].name} phase`);
  };

  const toggleListening = useCallback(() => {
    if (!recognitionRef.current) return;

    if (isListening) {
      recognitionRef.current.stop();
      setIsListening(false);
    } else {
      try {
        recognitionRef.current.start();
        setIsListening(true);
      } catch (error) {
        console.error("Failed to start recognition:", error);
        toast.error("Failed to start voice recognition");
      }
    }
  }, [isListening]);

  const handleSubmit = async (e?: React.FormEvent) => {
    e?.preventDefault();
    const messageContent = input.trim();
    if (!messageContent || isLoading || isCompleted) return;

    if (isListening && recognitionRef.current) {
      recognitionRef.current.stop();
      setIsListening(false);
    }

    const userMessage: Message = {
      id: Date.now().toString(),
      role: "user",
      content: messageContent,
      timestamp: new Date().toISOString(),
    };

    setMessages((prev) => [...prev, userMessage]);
    setInput("");
    setTranscript("");
    setIsLoading(true);

    try {
      const response = await fetch(`/api/interview/${interview.id}/chat`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ message: userMessage.content }),
      });

      if (!response.ok) {
        throw new Error("Failed to send message");
      }

      const reader = response.body?.getReader();
      if (!reader) throw new Error("No reader available");

      const assistantMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: "assistant",
        content: "",
        timestamp: new Date().toISOString(),
      };

      setMessages((prev) => [...prev, assistantMessage]);

      const decoder = new TextDecoder();
      let fullContent = "";

      while (true) {
        const { done, value } = await reader.read();
        if (done) break;

        const chunk = decoder.decode(value);
        const lines = chunk.split("\n");

        for (const line of lines) {
          if (line.startsWith("data: ")) {
            try {
              const data = JSON.parse(line.slice(6));
              if (data.content) {
                fullContent += data.content;
                setMessages((prev) => {
                  const updated = [...prev];
                  const lastMsg = updated[updated.length - 1];
                  if (lastMsg.role === "assistant") {
                    lastMsg.content = fullContent;
                  }
                  return updated;
                });
              }
            } catch {
              // Ignore parse errors
            }
          }
        }
      }

      // Check for phase transition marker in response
      const transitionMatch = fullContent.match(/\[PHASE_TRANSITION:\s*(\w+-?\w*)\]/);
      if (transitionMatch) {
        const nextPhaseId = transitionMatch[1];
        const nextPhaseIndex = INTERVIEW_PHASES.findIndex((p) => p.id === nextPhaseId);
        if (nextPhaseIndex !== -1 && nextPhaseIndex > currentPhaseIndex) {
          setTimeout(() => handlePhaseTransition(nextPhaseIndex), 1000);
        }
        // Remove the marker from displayed message
        setMessages((prev) => {
          const updated = [...prev];
          const lastMsg = updated[updated.length - 1];
          if (lastMsg.role === "assistant") {
            lastMsg.content = lastMsg.content.replace(/\[PHASE_TRANSITION:\s*\w+-?\w*\]/g, "").trim();
          }
          return updated;
        });
      }
    } catch {
      toast.error("Failed to send message. Please try again.");
      setMessages((prev) => prev.slice(0, -1));
    } finally {
      setIsLoading(false);
    }
  };

  const handleEndInterview = async () => {
    setIsEnding(true);
    try {
      const response = await fetch(`/api/interview/${interview.id}/end`, {
        method: "POST",
      });

      if (!response.ok) {
        throw new Error("Failed to end interview");
      }

      const data = await response.json();
      setScore(data.score);
      setShowEndDialog(false);
      toast.success("Interview completed! View your score below.");
      router.refresh();
    } catch {
      toast.error("Failed to end interview. Please try again.");
    } finally {
      setIsEnding(false);
    }
  };

  const progressPercentage = ((TOTAL_INTERVIEW_TIME - remainingTime) / TOTAL_INTERVIEW_TIME) * 100;

  return (
    <div className="h-screen flex flex-col bg-background">
      {/* Header */}
      <header className="border-b px-6 py-3">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Link href="/dashboard">
              <Button variant="ghost" size="sm">
                <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
                Back
              </Button>
            </Link>
            <div>
              <h1 className="font-semibold">{interview.topic}</h1>
              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <Badge variant="outline">{interview.difficulty}</Badge>
                {isCompleted ? (
                  <Badge variant="secondary">Completed</Badge>
                ) : (
                  <Badge className={currentPhase.color}>{currentPhase.name}</Badge>
                )}
              </div>
            </div>
          </div>

          <div className="flex items-center gap-6">
            {!isCompleted && (
              <>
                {/* Countdown Timer */}
                <div className="text-center">
                  <p className="text-xs text-muted-foreground mb-1">Time Remaining</p>
                  <p className={`font-mono text-2xl font-bold ${getTimeColor()}`}>
                    {formatTime(remainingTime)}
                  </p>
                </div>

                <Button variant="destructive" onClick={() => setShowEndDialog(true)}>
                  End Interview
                </Button>
              </>
            )}
          </div>
        </div>

        {/* Phase Progress Bar */}
        {!isCompleted && (
          <div className="mt-3">
            <div className="flex items-center gap-2 mb-2">
              {INTERVIEW_PHASES.map((phase, index) => (
                <div
                  key={phase.id}
                  className={`flex-1 text-center text-xs py-1 rounded ${
                    index === currentPhaseIndex
                      ? phase.color + " text-white"
                      : index < currentPhaseIndex
                      ? "bg-muted text-muted-foreground line-through"
                      : "bg-muted/50 text-muted-foreground"
                  }`}
                >
                  {phase.name}
                </div>
              ))}
            </div>
            <Progress value={progressPercentage} className="h-1" />
          </div>
        )}
      </header>

      {/* Time Warning Toast */}
      {showTimeWarning && (
        <div className="absolute top-20 left-1/2 transform -translate-x-1/2 z-50 bg-yellow-500 text-black px-6 py-3 rounded-lg shadow-lg animate-pulse">
          {showTimeWarning}
        </div>
      )}

      {/* Main Content */}
      <div className="flex-1 flex overflow-hidden">
        {/* Chat Area */}
        <div className="flex-1 flex flex-col">
          <ScrollArea className="flex-1 p-6" ref={scrollRef}>
            <div className="max-w-3xl mx-auto space-y-6">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={`flex ${message.role === "user" ? "justify-end" : "justify-start"}`}
                >
                  <div
                    className={`max-w-[80%] rounded-lg p-4 ${
                      message.role === "user"
                        ? "bg-primary text-primary-foreground"
                        : "bg-muted"
                    }`}
                  >
                    <p className="text-sm font-medium mb-1">
                      {message.role === "user" ? "You" : "Interviewer"}
                    </p>
                    <div className="whitespace-pre-wrap">{message.content}</div>
                  </div>
                </div>
              ))}

              {isLoading && (
                <div className="flex justify-start">
                  <div className="bg-muted rounded-lg p-4">
                    <p className="text-sm font-medium mb-1">Interviewer</p>
                    <div className="flex gap-1">
                      <span className="w-2 h-2 bg-foreground/50 rounded-full animate-bounce" />
                      <span className="w-2 h-2 bg-foreground/50 rounded-full animate-bounce" style={{ animationDelay: "0.1s" }} />
                      <span className="w-2 h-2 bg-foreground/50 rounded-full animate-bounce" style={{ animationDelay: "0.2s" }} />
                    </div>
                  </div>
                </div>
              )}
            </div>
          </ScrollArea>

          {/* Input Area */}
          {!isCompleted && (
            <div className="border-t p-4">
              <div className="max-w-3xl mx-auto">
                {/* Live Transcription Display */}
                {isListening && transcript && (
                  <div className="mb-3 p-3 bg-primary/10 border border-primary/20 rounded-lg">
                    <p className="text-sm text-muted-foreground mb-1">Listening...</p>
                    <p className="text-foreground italic">{transcript}</p>
                  </div>
                )}

                {/* Input Mode Toggle */}
                <div className="flex items-center justify-between mb-3">
                  <div className="flex items-center gap-2">
                    {speechSupported && (
                      <div className="flex items-center gap-1 bg-muted rounded-lg p-1">
                        <button
                          onClick={() => {
                            setInputMode("voice");
                            if (!isListening) toggleListening();
                          }}
                          className={`px-3 py-1.5 rounded-md text-sm font-medium transition-colors ${
                            inputMode === "voice"
                              ? "bg-primary text-primary-foreground"
                              : "text-muted-foreground hover:text-foreground"
                          }`}
                        >
                          Voice
                        </button>
                        <button
                          onClick={() => {
                            setInputMode("keyboard");
                            if (isListening && recognitionRef.current) {
                              recognitionRef.current.stop();
                              setIsListening(false);
                            }
                          }}
                          className={`px-3 py-1.5 rounded-md text-sm font-medium transition-colors ${
                            inputMode === "keyboard"
                              ? "bg-primary text-primary-foreground"
                              : "text-muted-foreground hover:text-foreground"
                          }`}
                        >
                          Keyboard
                        </button>
                      </div>
                    )}
                    {isAnalyzingPhase && (
                      <span className="text-xs text-muted-foreground animate-pulse">
                        Analyzing previous phase...
                      </span>
                    )}
                  </div>
                  {inputMode === "voice" && speechSupported && (
                    <p className="text-xs text-muted-foreground">
                      {isListening ? "Speak now..." : "Click mic to start"}
                    </p>
                  )}
                </div>

                <form onSubmit={handleSubmit}>
                  <div className="flex gap-3 items-end">
                    {speechSupported && (
                      <button
                        type="button"
                        onClick={toggleListening}
                        disabled={isLoading}
                        className={`flex-shrink-0 w-14 h-14 rounded-full flex items-center justify-center transition-all ${
                          isListening
                            ? "bg-red-500 hover:bg-red-600 text-white animate-pulse"
                            : "bg-primary hover:bg-primary/90 text-primary-foreground"
                        } disabled:opacity-50`}
                      >
                        {isListening ? (
                          <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 10a1 1 0 011-1h4a1 1 0 011 1v4a1 1 0 01-1 1h-4a1 1 0 01-1-1v-4z" />
                          </svg>
                        ) : (
                          <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11a7 7 0 01-7 7m0 0a7 7 0 01-7-7m7 7v4m0 0H8m4 0h4m-4-8a3 3 0 01-3-3V5a3 3 0 116 0v6a3 3 0 01-3 3z" />
                          </svg>
                        )}
                      </button>
                    )}

                    <div className="flex-1">
                      <Textarea
                        value={input}
                        onChange={(e) => setInput(e.target.value)}
                        placeholder={
                          inputMode === "voice"
                            ? "Voice transcription appears here... or type manually"
                            : "Type your response... (Enter to send)"
                        }
                        className="resize-none"
                        rows={3}
                        onKeyDown={(e) => {
                          if (e.key === "Enter" && !e.shiftKey) {
                            e.preventDefault();
                            handleSubmit(e);
                          }
                        }}
                        disabled={isLoading}
                      />
                    </div>

                    <Button type="submit" disabled={isLoading || !input.trim()} className="flex-shrink-0 h-14 px-6">
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8" />
                      </svg>
                    </Button>
                  </div>
                </form>
              </div>
            </div>
          )}
        </div>

        {/* Right Sidebar - Phase Analysis & Score */}
        {(phaseAnalyses.length > 0 || (isCompleted && score)) && (
          <div className="w-80 border-l overflow-auto p-4">
            {/* Phase Analyses */}
            {phaseAnalyses.length > 0 && !isCompleted && (
              <div className="mb-6">
                <h3 className="font-semibold mb-3">Phase Scores</h3>
                <div className="space-y-3">
                  {phaseAnalyses.map((analysis) => (
                    <div key={analysis.phase} className="p-3 bg-muted rounded-lg">
                      <div className="flex items-center justify-between mb-1">
                        <span className="text-sm font-medium capitalize">
                          {analysis.phase.replace("-", " ")}
                        </span>
                        <Badge variant={analysis.score >= 3 ? "default" : analysis.score >= 2 ? "secondary" : "destructive"}>
                          {analysis.score}/4
                        </Badge>
                      </div>
                      <p className="text-xs text-muted-foreground">{analysis.summary}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Final Score */}
            {isCompleted && score && <ScoreCard score={score} />}
          </div>
        )}
      </div>

      {/* End Interview Dialog */}
      <Dialog open={showEndDialog} onOpenChange={setShowEndDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>End Interview?</DialogTitle>
            <DialogDescription>
              {remainingTime > 0 ? (
                <>You still have {formatTime(remainingTime)} remaining. </>
              ) : null}
              The AI will evaluate your performance and provide a detailed score.
            </DialogDescription>
          </DialogHeader>
          <DialogFooter>
            <Button variant="outline" onClick={() => setShowEndDialog(false)}>
              Continue Interview
            </Button>
            <Button onClick={handleEndInterview} disabled={isEnding}>
              {isEnding ? "Evaluating..." : "End & Get Score"}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}
